export default {
	data() {
		return {
			toolList: []
		}
	},
	onLoad() {
		this.getToolList();
		uni.startPullDownRefresh();
	},
	methods: {
		getToolList() {
			var query = this.$Bmob.Query('product');
			this.toolList = [];
			let arr = [];
			
			query.order('-endTime');
			query.equalTo('notDelete', '==', true);
			query.equalTo('status', '==', 0);
			query.find().then((res) => {
				uni.stopPullDownRefresh();
				// that.loading = false;
				for (let i = 0; i < res.length; i += 1) {          
					arr.push({
						id: res[i].objectId,
						imgSrc: res[i].imgSrc,
						title: res[i].title,
						desc: res[i].desc,
					});
				}
				this.toolList = arr;
			});
		},
		onPullDownRefresh() {
				console.log('refresh');
				// setTimeout(function () {
				// 		uni.stopPullDownRefresh();
				// }, 1000);
				this.getToolList();
		},
		goBuy(id) {
			uni.navigateTo({
			  url: `../toolItem/toolItem?id=${id}`
			});
		}
	}
}
